<template>
<div>
	<div>
		<v-layout row wrap>	
			<v-flex xs12 sm6 md4 lg4 xl4
					v-for="showOffObj in  showOffList" v-bind:key="idx">
			<v-card
			    max-width="344"
			    class="mx-auto"
			  >
			    <v-list-item>
			      <v-list-item-avatar color="grey">
			      	<img
			      		v-if="showOffObj.mbrJpa.mbrRpstImgUrl != null &&  showOffObj.mbrJpa.mbrRpstImgNm != null"
			      		:src="showOffObj.mbrJpa.mbrRpstImgUrl + '/' + showOffObj.mbrJpa.mbrRpstImgNm"
			      	>
			      	<img
			      		v-else
			      		src="@/assets/anonymous.png"
			      	>
			      </v-list-item-avatar>
			      <v-list-item-content>
			        <v-list-item-title v-text="showOffObj.mbrJpa.mbrNickNm"></v-list-item-title>
			        <v-list-item-subtitle v-text="timeForToday(showOffObj.regDt)"></v-list-item-subtitle> 
			      </v-list-item-content>
			    </v-list-item>
			    <template v-if="showOffObj.showOffAttachJpa.length > 1">
			    	<swiper ref="mySwiper" 
						class="swiper"
						:options="swiperOptions" 
						v-if="showOffObj.showOffAttachJpa != null" 
					> 
					    <swiper-slide v-for="showOffAttachObj in showOffObj.showOffAttachJpa" v-bind:key="subIndx">
				    		 <v-img 
						      :src="showOffAttachObj.showOffAttachUrl + '/' + showOffAttachObj.showOffAttachNm"
						      height="295"
						    ></v-img>
					    </swiper-slide>
				    </swiper>
				    <div class="swiper-pagination" slot="pagination"></div>
			    </template>
			    <template v-else-if="showOffObj.showOffAttachJpa.length == 1">
			    	<v-img 
					      :src="showOffObj.showOffAttachJpa[0].showOffAttachUrl + '/' + showOffObj.showOffAttachJpa[0].showOffAttachNm"
					      height="295"
				    ></v-img>
			    </template>
			    <v-card-text v-html="showOffObj.showOffCont">
			    </v-card-text>
			
			    <v-card-actions>
			      <v-btn
			        text
			        color="deep-purple accent-4"
			      >
			        Read
			      </v-btn>
			      <v-btn
			        text
			        color="deep-purple accent-4"
			      >
			        Bookmark
			      </v-btn>
			      <v-spacer></v-spacer>
			      <v-btn icon>
			        <v-icon>mdi-heart</v-icon>
			      </v-btn>
			      <v-btn icon>
			        <v-icon>mdi-share-variant</v-icon>
			      </v-btn>
			    </v-card-actions>
			  </v-card>
			</v-flex>
		</v-layout>
		<div>
			<v-layout v-if="loading" xs12 sm12 md12 lg12 xl12 class="text-center">
				<v-flex>
					<v-progress-circular
				      indeterminate
				      color="primary"
				    ></v-progress-circular>
				</v-flex>
			</v-layout>
		</div>
	</div>
	
</div>
	
</template>

<script>
import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper'
import InfiniteLoading from 'vue-infinite-loading'

  export default {
    name: 'ShowOffList',
    components : {
    	Swiper,
    	SwiperSlide,
    	InfiniteLoading
    },
    directives: {
        swiper: directive
    },
   	data : function(){
   		return {
   			swiperOptions: {
             slidesPerView: 1,
   	          pagination: {
   	            el: '.swiper-pagination',
   	          },
   			},
   			show : false,
   			showOffList : [],
   			currentPage : 0,
   			listPageSize : 6,
   			loading : false,
   			loading : false,
   			bottom: false,
   		}
   	},
   	computed: {
        swiper() {
          return this.$refs.mySwiper.$swiper
        }
    },
   	created : function(){
   		var that = this;
   		
		that.getFirstShowOffListPage();
		
   	},
   	mounted : function() {
   		var that = this;
   		window.addEventListener('scroll', function(){
   			that.bottom = that.bottomVisible()
        });
   	},
   	watch: {
   	    bottom(bottom) {
   	      var that =this;
   	      if (bottom) {
   	    	that.getNextShowOffListPage();
   	      }
   	    }
   	 },
   	methods : {
   	 	bottomVisible : function(){
         const scrollY = window.scrollY
         const visible = document.documentElement.clientHeight
         const pageHeight = document.documentElement.scrollHeight - 110
         const bottomOfPage = visible + scrollY >= pageHeight
         console.log(bottomOfPage)
          console.log( pageHeight < visible)
         return bottomOfPage || pageHeight < visible
         
       },
   		getFirstShowOffListPage: function(){ //처음 인입시 리스트 로드
   			
   			var that = this;
   	   		
   			this.$store.dispatch('PageGetter',{
   				url : '/api/showOff/list',
   				size : that.listPageSize,
   				page : that.currentPage
   			})
   			.then(resp =>{
   				that.showOffList =  resp;
   			})
   			.catch(err => {
   				console.log("pageGetter error");
   			})
   			
   		},
		getNextShowOffListPage: function($state){//처음 이후 스크롤시  리스트 로드
			
   			var that = this;
			that.loading = true;
   			that.currentPage +=1;
   			this.$store.dispatch('PageGetter',{
   				url : '/api/showOff/list',
   				size : that.listPageSize,
   				page : that.currentPage
   			})
   			.then(resp =>{
   				setTimeout(function(){
   					if(resp.length > 0 ){
   						var mergeList = that.showOffList.concat(resp);
   						that.showOffList = mergeList;
   						that.loading = false;
   					}else{
   						that.loading = false;
   					}
   				},1000);
   			})
   			.catch(err => {
   				console.log("newt pageGetter error");
   			})
   			
   		},
   		timeForToday : function(value) {
	   		var today = new Date();
	        var timeValue = new Date(value);
	        
	        var betweenTime = Math.floor((today.getTime() - timeValue.getTime()) / 1000 / 60);
	        if (betweenTime < 1) return '방금전';
	        if (betweenTime < 60) {
	            return betweenTime + '분전';
	        }
	        
	        var betweenTimeHour = Math.floor(betweenTime / 60);
	        if (betweenTimeHour < 24) {
	            return betweenTimeHour + '시간전';
	        }
	        
	        var betweenTimeDay = Math.floor(betweenTime / 60 / 24);
	        if (betweenTimeDay < 365) {
	            return betweenTimeDay + '일전';
	        }
	        
	        return String(Math.floor(betweenTimeDay / 365)) + '년전';

   		}
   		
   	}
  }
</script>
